export function createNewAbortController(
  abortControllerRef: React.RefObject<AbortController | null>
): { controller: AbortController; signal: AbortSignal } {
  if (abortControllerRef.current) {
    abortControllerRef.current.abort(); // –û—Ç–º–µ–Ω—è–µ–º –ø—Ä–µ–¥—ã–¥—É—â–∏–µ –∑–∞–ø—Ä–æ—Å—ã
  }
  abortControllerRef.current = new AbortController(); // –°–æ–∑–¥–∞—ë–º –Ω–æ–≤—ã–π AbortController
  return {
    controller: abortControllerRef.current,
    signal: abortControllerRef.current.signal,
  };
}
