import { classNames } from "shared/lib/classNames/classNames";
import cls from "./FenInput.module.scss";
import { Input } from "../Input/Input";
import { ChangeEvent, useState } from "react";
import { Button, ButtonTheme } from "../Button/Button";
import copyImg from "shared/assets/copy.png";
import krestImg from "shared/assets/krest.png";

interface FenInputProps {
  className?: string;
  fenPosition: string;
  onChange: (fen: string) => void;
}

export const FenInput = ({
  className,
  fenPosition,
  onChange,
}: FenInputProps) => {
  const [isNull, setIsNull] = useState(false);

  const handleFenInputChange = (e: ChangeEvent<HTMLInputElement>) => {
    const fen = e.target.value;
    if (fen === "") return;
    setIsNull(false);
    if (onChange) {
      onChange(fen);
    }
  };

  const copy = () => {
    navigator.clipboard.writeText(fenPosition);
    console.log("copied");
  };

  const deleteFen = () => {
    setIsNull(true);
  };

  return (
    <div className={classNames(cls.FenInput, {}, [className])}>
      <Input
        value={isNull ? "" : fenPosition}
        onChange={handleFenInputChange}
        placeholder="–í—Å—Ç–∞–≤—å—Ç–µ –ø–æ–∑–∏—Ü–∏—é FEN, —á—Ç–æ–±—ã –Ω–∞—á–∞—Ç—å —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ"
      />
      <Button onClick={deleteFen} theme={ButtonTheme.CLEAR} square>
        <img src={krestImg} width={32} alt="–£–¥–∞–ª–∏—Ç—å" />
      </Button>
      <Button
        onClick={copy}
        theme={ButtonTheme.CLEAR}
        className={cls.copyButton}
        square
      >
        <img src={copyImg} alt="–ö–æ–ø–∏—Ä–æ–≤–∞—Ç—å" />
      </Button>
    </div>
  );
};
